<template>
  <with-app-bar>
    <v-card-text class="blue">
      <v-sheet max-width="90%" max-height="100%" height="300" class="mx-auto"></v-sheet>
    </v-card-text>
    <v-row lign="center" justify="center" class="mx-auto">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-1 justify-center pa-5">
          <v-form ref="form" lazy-validation>
            <v-text-field
              v-model="username"
              name="username"
              label="用户名"
              required
              prepend-icon="person"
              @keydown.enter="post_login()"
            ></v-text-field>

            <v-text-field
              v-model="password"
              name="password"
              prepend-icon="lock"
              type="password"
              label="密码"
              required
              @keydown.enter="post_login()"
            ></v-text-field>
            <v-card-actions class="pt-2 py-4">
            <v-btn text color="success" class="mr-4" @click="recaptcha()">登入</v-btn>

            <v-btn text color="blue" dark class="mr-4" @click="go_register()">注册</v-btn>

            <v-btn text color="error" class="mr-4" @click="go_repass()">找回密码</v-btn>
            </v-card-actions>
          </v-form>
          <v-divider></v-divider>
          <v-card-subtitle>
              This site is protected by reCAPTCHA and the Google
              <a
                href="https://policies.google.com/privacy"
              >Privacy Policy</a> and
              <a href="https://policies.google.com/terms">Terms of Service</a> apply.
            </v-card-subtitle>
        </v-card>
      </v-col>
    </v-row>
  </with-app-bar>
</template>

<script>
import { Login } from "../request/api";
export default {
  name: "Login",
  props: {
    source: String
  },
  data() {
    return {
      username: "",
      password: "",
      tokentimestate: false,
      tokentime: 0
      //retoken: "",
    };
  },
  mounted() {
    if (localStorage.getItem("token")) {
      this.$router.push("/");
    }
  },
  watch: {
    tokentimestate(val) {
      if (val == true) {
        this.tokentime = 1;
      }
    }
  },
  methods: {
    go_register() {
      this.$router.push("/Register");
    },
    go_repass() {
      this.$router.push("/RePass");
    },
    async recaptcha() {
      // (optional) Wait until recaptcha has been loaded.
      await this.$recaptchaLoaded();
      // Execute reCAPTCHA with action "login".
      this.retoken = await this.$recaptcha("login");
      this.post_login();
    },
    post_login() {
      Login({
        username: this.username,
        password: this.password,
        retoken: this.retoken
      }).then(res => {
        localStorage.setItem("token", res.data.token);
        localStorage.setItem("username", res.data.username);
        this.$store.commit("pushprofile", res.data);
        this.$snackbar.success(res.message);
        if (this.$route.params.from) {
          this.$router.push(this.$route.params.from);
        } else {
          this.$router.push("/");
        }
      });
    }
  },
  template: '<button @click="recaptcha">Execute recaptcha</button>'
};
</script>

<style>
.login {
  width: 100%;
  height: 100%;
}
</style>